import {
  Box, Paper, Stack, Typography,
} from '@mui/material';

function TopBar() {
  return (
    <Paper
      sx={{
        position: 'fixed', top: 0, left: 0, right: 0,
      }}
      elevation={3}
    >
      <Stack
        direction="row"
        justifyContent="space-between"
        alignItems="center"
        spacing={2}
      >
        <Box>
          <Typography>
            2023
          </Typography>
          <Typography>
            January
          </Typography>
        </Box>
        <Box>
          <Typography>
            1   
          </Typography>
          <Typography>
            600,000
          </Typography>
        </Box>
        <Box>
          <Typography>
            1 1
          </Typography>
          <Typography>
            
          </Typography>
        </Box>
      </Stack>
    </Paper>
  );
}
export default TopBar;
